.Amenities-hero{
  width: 100%;
  max-height: 45vh;
  object-fit: cover;
  background-attachment: fixed;
}
.Amenities-hero::after{
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.Amenities-call-out{
  width: 85vw;
  margin: auto;
  display: grid;
  grid-gap: 2em;
  grid-template-rows: 1fr 1fr;
  grid-template-areas:'text' 'img';
  overflow: hidden;
  min-height: 45vh;
}

.Amenities-call-out > p{
  width: 100%;
  min-height: 15vh;
  color: black;
  background: none;
  justify-self: center;
  text-align: justify;
  grid-area: text;
}
.Amenities-call-out >img{
  grid-area: img;
  object-fit: cover;
  object-position: center;
  justify-self: center;
  height:100%;
  width:85%;
  margin-bottom: 2em;
}

@media only screen and (min-width: 700px){
  .Amenities-call-out{
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 'text img';
  }
  .Amenities-call-out > p{
    width: 25vw;
    color: black;
    background: none;
    margin-left: 2em;
    justify-self:end;
    text-align: justify;
    grid-area: text;

  }
  .Amenities-call-out >img{
    grid-area: img;
    object-fit: cover;
    object-position: center;
    justify-self: start;
    height:100%;
    width:80%;
  }
}
