body {
  align-content: flex-start;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background:#eee;
}

.App {
  text-align: center;
}
.Amenities, .Properties, .homepage, .ResPortal {
  margin-top: 4em;
}
.Features{
  margin: 2em;
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 2em;
}
.Tsection{
  background-color: black;
  height: 30vh;
  border-radius: .5em;
}
