
p {
  font-size: 1em;
  color: white;
  background-color: rgba(25, 34, 54, .5);
}

button {
  margin: 0.8em;
  color: white;
  font-size: 1.1em;
  border-radius: 25px;
  padding: 1em;
  border: none;
  background: #eb7873;
  transition: .75s ease;
}
button:hover{
  background:palevioletred;
  color: white;
  cursor: pointer;
  transform: scale(1.1);
}
