.Hamburger{
  display:grid;
  grid-template-rows: repeat(3, 1fr);
  grid-row-gap: .25em;
  width: 2em;
  grid-area: hamburger;
  align-self: center;
  cursor: pointer;
  margin-right:5em ;

}
.Hamburger:hover {
  transform: scale(1.05);
}
.Hamburger > div {
  transition: .2s;
  background-color: white;
  height: .25em;
}
.Exit.Hamburger  {
  display:grid;
  grid-template-rows: repeat(3, 1fr);
  grid-row-gap: .25em;
  width: 2em;
  grid-area: hamburger;
  align-self: center;
  cursor: pointer;
  z-index: 4;
  padding-bottom: 1.5em;
}
.Exit.Hamburger  > div.middle {
  opacity: 0;
  transition: opacity .7s;

}
.Exit.Hamburger  > div.top{
  transform: translateY(1em) rotate(45deg);
}
.Exit.Hamburger > div.bottom{
  transform: rotate(-45deg);
}
.Navigation {
  height: 1em;
  width: 100vw;
  top: 0;
  padding-bottom: 3em;
  display: grid;
  grid-template-columns: 4em auto auto 4em;
  grid-template-areas:
    "margin logo space hamburger";
  background-color: #333333;
  position:fixed;
  z-index: 3;
  text-decoration: none;
  box-shadow: 0 7px 9px 0 rgba(0, 0, 0, 0.2);
}
.Desktop {
  display: none;
}

@media only screen and (min-width: 700px){
  .Navigation{
    grid-template-columns: 2em 8em auto 3em 4em;
    grid-template-areas: "margin logo desktop space hamburger";
  }
  .Desktop {
    display: inline;
    grid-area: desktop;
    justify-self: end;
    align-self: start;
    padding-top: 5px;
  }
  .phone-icon, .email-icon {
     height: 1.25em;
     margin-bottom: -.2em;
  }
  .email {
    color: white;
    cursor: pointer;
    height: 2em;
    font-weight: bold;
    padding-top: 1em;
    transition-duration: .25s;
  }
  .email:hover{
    transform: scale(1.025);
    color: #b3b3b3;
  }

}
.logo{
  grid-area: logo;
  cursor: pointer;
  font-weight: bold;
  color: white;
}

.OpenNavigation{
  background-color: rgba(51, 51, 51, .8);
  height: 100vh;
  width: 100vw;
  position:fixed;
  display: grid;
  grid-template-columns: auto 3em 4em;
  grid-auto-rows: 3em repeat(12 1fr);
  grid-template-areas:
    "space hamburger"
    "MenuItem1  margin"
    "MenuItem2  margin"
    "MenuItem3  margin"
    "MenuItem4  margin"
    "MenuItem5  margin"
    "MenuItem6  margin"
    "MenuItem7  margin"
    "MenuItem8  margin"
    "MenuItem9  margin"
    "MenuItem10  margin"
    "MenuItem11 margin"
    "MenuItem12  margin";
  z-index: 3;
}
.Menu{
  align-self: end;
  justify-self: end;
  font-weight: bold;
  text-decoration: none;
  color: white;
  font-size: 2em;
  transition: .5s ease;
}
.Menu:hover {
  transform: scale(1.12);
}
.Exit {
  align-self: center;
  cursor: pointer;
  justify-self: start;
}
.MenuItem1{
  grid-area:MenuItem1;
}
.MenuItem2{
  grid-area:MenuItem2;
}
.MenuItem3{
  grid-area:MenuItem3;
}
.MenuItem4{
  grid-area:MenuItem4;
}
